import states from '../reducers/statesInventory';
import { onLoading, hasError, onSuccess, onAuthenticating, renderAllowedApplications, changepnsdata } from './statesChanging';
import {
  login as pnsLogin,
  getAllowedApplications as getApplications,
  logout as pnsLogout,
  updateprofile as updateprofileapi
} from '../services/pns'
import Cookie from 'universal-cookie'
const cookie = new Cookie();


/**
 * method to handle PNS log in API
 * @param  {Object} data [data parsing to API]
 *    @param {String} authorization   [NIP or username of PNS]
 *    @param {String} passowrd        [account password]
 * @return {[type]}      [description]
 */
export function logPnsIn (data) {
  return (dispatch)=> {
    dispatch(onLoading(states.onFormLoading, true, 'Loading ...'));
    dispatch(hasError(states.hasFormError, false));
    pnsLogin(data, (err, res)=> {
      dispatch(onLoading(states.onFormLoading, false));
      if (err) {
        dispatch(hasError(states.hasFormError, true, res));
      } else {
        if (res.code === 201) {
          dispatch(onSuccess(true, 'Mengalihkan Anda ke Dashboard, mohon tunggu ...'));
          localStorage.setItem('isAuthenticated', 'authenticated');
          cookie.set('kemenkumhamssoengine', res.data.token, {
            domain: 'kemenkumham.go.id'
          })
          cookie.set('kemenkumhamssoengine_pns', {nip: res.data.pns.nip}, {
            domain: 'kemenkumham.go.id'
          })
          setTimeout(()=> {
            dispatch(onAuthenticating('authenticated'));
            dispatch(renderAllowedApplications(res.data.applications));
            dispatch(changepnsdata(res.data.pns));
          }, 2000);
        } else {
          dispatch(hasError(states.hasFormError, true, res.message, res.key))
        }
      }
    })
  }
}

export function logPnsOut () {
  return (dispatch)=> {
    dispatch(onLoading(states.onAppLoading, true, {title: "Terima Kasih", message: "Server sedang membersihkan data anda, mohon tunggu ..."}));
    pnsLogout((err, res)=> {
      if (!err) {
        localStorage.clear();
        cookie.remove('kemenkumhamssoengine', {
          domain: 'kemenkumham.go.id'
        })
        cookie.remove('kemenkumhamssoengine_pns', {
          domain: 'kemenkumham.go.id'
        })
        setTimeout(()=> {
          dispatch(onLoading(states.onAppLoading, false))
          dispatch(onAuthenticating(null));
          dispatch(renderAllowedApplications([]));
          dispatch(changepnsdata({}))
        }, 1000);

      }
    })
  }
}

/**
 * method to get the users allowed application based on mapping concepts in database
 * @return {[type]} [description]
 */
export function getAllowedApplications () {
  return (dispatch)=> {
    dispatch(onLoading(states.onAppLoading, true, {title: 'Selamat Datang', message: 'Sedang melakukan pembaruan data anda, mohon tunggu ...'}));
    getApplications((err, res)=> {
      mainCallback(dispatch, err, res, (err, res)=> {
        if (res.code === 200) {
          dispatch(onLoading(states.onAppLoading, false));
          dispatch(renderAllowedApplications(res.data))
        }
      })
    })
  }
}

export function updateprofile (data, key = '') {
  return (dispatch)=> {
    dispatch(changepnsdata(false, cookie.get('kemenkumhamssoengine_pns')));
    dispatch(onLoading(states.onFormLoading, true, 'Memperbaharui ...', key));
    dispatch(onSuccess(false));
    dispatch(hasError(states.hasFormError, false))
    updateprofileapi(data, (err, res)=> {
      mainCallback(dispatch, err, res, (err, res)=> {
        setTimeout(()=> {
          dispatch(onLoading(states.onFormLoading, false));
          if (res.code === 201) {
            dispatch(changepnsdata(true, res.pns));
            dispatch(onSuccess(true, res.message, res.key));
          } else {
            dispatch(hasError(states.hasFormError, true, res.message, res.key))
          }
        }, 1000);
      })
    })
  }
}

/**
 * main callback to handle the global callback of API
 * @param  {[type]} dispatch    [description]
 * @param  {[type]} err         [description]
 * @param  {[type]} res         [description]
 * @param  {[type]} newcallback [description]
 * @return {[type]}             [description]
 */
function mainCallback(dispatch, err, res, newcallback) {
  if (res.code === 401) {
    dispatch(onLoading(states.onAppLoading, true, {title: "Session Anda Berakhir, Silahkan Login Kembali", message: "Server sedang membersihkan data anda, mohon tunggu ..."}));
    localStorage.clear();
    cookie.remove('kemenkumhamssoengine', {
      domain: 'kemenkumham.go.id'
    })
    cookie.remove('kemenkumhamssoengine_pns', {
      domain: 'kemenkumham.go.id'
    })
    setTimeout(()=> {
      dispatch(onLoading(states.onAppLoading, false))
      dispatch(onAuthenticating(null, {}))
    }, 2000)
  } else {
    if (res.newtoken != null) {
      cookie.set('kemenkumhamssoengine', res.newtoken, {
        domain: 'kemenkumham.go.id'
      })
    }
    newcallback(err, res)
  }
}



