import Cookie from 'universal-cookie';
import jwt from 'jsonwebtoken';

export const secretJWT = 'pusdatinforindonesia'

const cookie = new Cookie();

export default {
  isAuthenticated: (localStorage.getItem('isAuthenticated') !== null && cookie.get('kemenkumhamssoengine_pns') != null && cookie.get('kemenkumhamssoengine') != null),
  pnsdata: cookie.get('kemenkumhamssoengine') == null ? {} : jwt.verify(cookie.get('kemenkumhamssoengine'), secretJWT).pns,
  allowedApplications: cookie.get('kemenkumhamssoengine') == null ? [] : jwt.verify(cookie.get('kemenkumhamssoengine'), secretJWT).allowedApplication,
  apiProcess: {
    isLoading: {
      form: {
        status: false,
        message: '',
        key: ''
      }, app: {
        status: false,
        message: ''
      }
    },
    hasError: {
      form: {
        status: false,
        message: '',
        key: ''
      }
    },
    isSuccess: {
      status: false,
      message: '',
      key: ''
    }
  }
}



